@import "https://fonts.googleapis.com/css2?family=Podkova:wght@400..800&family=Ubuntu+Condensed&display=swap";
@import "https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap";
.container {
  box-sizing: border-box;
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  padding: 0 8px;
}

@media (width >= 320px) {
  .container {
    max-width: 320px;
    padding: 0 10px;
  }
}

@media (width >= 768px) {
  .container {
    max-width: 768px;
    padding: 0 12px;
  }
}

@media (width >= 1200px) {
  .container {
    max-width: 1200px;
    padding: 0 15px;
  }
}

.footer__text {
  color: #fff;
}

.header {
  box-sizing: border-box;
  width: 100%;
  height: 64px;
  font-family: var(--podkova-font);
  backdrop-filter: blur(6px);
  z-index: 1000;
  padding: 0 16px;
  padding-top: env(safe-area-inset-top, 0px);
  background-color: #3c3c3ccc;
  justify-content: space-between;
  align-items: center;
  transition: background-color .3s, box-shadow .3s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 4px 10px #0006;
}

@media screen and (width >= 1200px) {
  .header {
    padding: 0 24px;
  }
}

@media (width <= 768px) {
  header {
    backdrop-filter: blur(10px);
  }
}

.flex {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__logo {
  border-radius: 50%;
  width: 50px;
  height: 50px;
}

@media screen and (width >= 1200px) {
  .header__logo {
    width: 60px;
    height: 60px;
  }
}

.header__burger {
  cursor: pointer;
  background: none;
  border: 0;
  margin-right: 8px;
  padding: 8px;
  display: none;
}

.header__burger-icon {
  width: 28px;
  height: 28px;
  color: var(--text-color);
  transition: transform .25s, opacity .2s;
}

.header__burger.open .header__burger-icon {
  transform: rotate(90deg)scale(1.05);
}

.header__nav {
  padding-left: 12px;
  padding-right: 12px;
}

.header__panel {
  background: none;
  border-radius: 8px;
  align-items: center;
  gap: 12px;
  padding: 8px 12px;
  display: flex;
  position: relative;
}

.header__link {
  color: var(--text-color);
  font-size: 1rem;
  font-weight: 500;
  text-decoration: none;
  transition: color .2s, background .2s;
  display: inline-block;
}

@media (width <= 768px) {
  .header__burger {
    display: inline-flex;
  }

  .header__nav {
    z-index: 1200;
    justify-content: flex-start;
    align-items: flex-start;
    display: none;
    position: fixed;
    inset: 0;
  }

  .header__nav.open {
    display: flex;
  }

  .header__nav .header__panel {
    width: 100%;
    color: var(--text-color);
    padding: calc(env(safe-area-inset-top, 20px)  + 36px) 18px 18px;
    box-shadow: none;
    backdrop-filter: none;
    z-index: 1250;
    box-sizing: border-box;
    background: #0c0c0cf5;
    border-radius: 0;
    flex-direction: column;
    gap: 18px;
    display: flex;
    position: relative;
    overflow-y: auto;
  }

  .header__nav .header__list {
    flex-direction: column;
    align-items: stretch;
    gap: 24px;
    margin: 0;
    padding: 0 12px;
    display: flex;
    width: 100% !important;
    max-width: none !important;
  }

  .header__nav .header__list .header__link {
    color: var(--text-color);
    text-align: center;
    padding: 12px 10px;
    font-size: 1.05rem;
  }

  .header__nav .header__list .header__item {
    width: 100%;
  }
}

.mobile-modal {
  display: none;
}

.mobile-modal.open {
  display: block;
}

.mobile-modal__backdrop {
  z-index: 1200;
  background: #0000008f;
  position: fixed;
  inset: 0;
}

.mobile-modal__content {
  width: 92%;
  max-width: 440px;
  color: var(--text-color);
  z-index: 1250;
  background: #0c0c0ce0;
  border-radius: 12px;
  flex-direction: column;
  gap: 12px;
  padding: 20px 18px 26px;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 18px 50px #0009;
}

.mobile-modal__close {
  color: var(--text-color);
  cursor: pointer;
  background: none;
  border: 0;
  font-size: 24px;
  line-height: 1;
  position: absolute;
  top: 10px;
  right: 10px;
}

.mobile-modal__nav .header__list {
  flex-direction: column;
  align-items: center;
  gap: 12px;
  margin: 16px 0 0;
  display: flex;
}

.mobile-modal__nav .header__link {
  text-align: center;
  width: 100%;
  padding: 8px 6px;
  font-size: 1.05rem;
  display: block;
}

.mobile-modal__nav .header__list {
  align-items: stretch;
  width: 100%;
  max-width: 420px;
  margin: 16px auto 0;
}

.mobile-modal__nav .header__list .header__item {
  width: 100%;
}

.mobile-modal__nav .header__list .header__link {
  width: 100%;
  padding: 12px 14px;
  display: block;
}

@media (width >= 769px) {
  .mobile-modal {
    display: none !important;
  }

  .header__panel-close, .mobile-modal__close {
    visibility: hidden;
    display: none !important;
  }
}

.header__panel-close {
  color: var(--text-color);
  cursor: pointer;
  background: none;
  border: 0;
  justify-content: center;
  align-items: center;
  padding: 6px;
  display: inline-flex;
  position: absolute;
  top: 12px;
  right: 12px;
}

.header__panel {
  position: relative;
}

.header__panel-close svg {
  opacity: .95;
}

.header__panel-close:hover {
  background: #ffffff0a;
  border-radius: 8px;
}

.header__panel-close:focus {
  border-radius: 8px;
  outline: 2px solid #ffffff1f;
}

.panel--hidden {
  opacity: 0;
  display: none !important;
  transform: none !important;
}

.header__overlay-close {
  z-index: 1260;
  color: var(--text-color);
  cursor: pointer;
  background: #ffffff0a;
  border: 0;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  display: inline-flex;
  position: fixed;
  top: 18px;
  right: 18px;
  box-shadow: 0 6px 18px #00000073;
}

.header__overlay-close:hover {
  background: #ffffff0f;
}

.header__overlay-close svg {
  opacity: .95;
}

.burger__bar {
  background: var(--text-color);
  width: 22px;
  height: 2px;
  margin: 4px 0;
  transition: transform .25s, opacity .2s;
  display: block;
}

@media (width <= 768px) {
  .header__list {
    flex-direction: column;
    gap: 52px;
    padding: 16px;
    display: none;
  }

  .header__burger {
    display: inline-flex;
  }

  .header__nav.open .header__list {
    z-index: 1000;
    background: #0000000a;
    border-radius: 8px;
    min-width: 160px;
    display: flex;
    top: 100%;
    right: 16px;
    box-shadow: 0 8px 20px #0000001f;
  }
}

.header__link {
  color: var(--text-color);
  text-align: left;
  padding-bottom: 4px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
  position: relative;
}

@media screen and (width <= 768px) {
  .header__link {
    width: 100%;
  }
}

@media screen and (width >= 1200px) {
  .header__link {
    text-align: center;
    width: 100%;
  }
}

.header__link:hover {
  color: var(--text-color) !important;
}

.header__link:after {
  content: "";
  transform-origin: 100%;
  background-color: #ff0;
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media screen and (width >= 1200px) {
  .header__link:after {
    content: "";
    transform-origin: 100%;
    background-color: #ff0;
    width: 100%;
    height: 2px;
    transition: transform .3s;
    position: absolute;
    bottom: 0;
    left: 0;
    transform: scaleX(0);
  }
}

.header__link:hover:after {
  transform-origin: 0;
  transform: scaleX(1);
}

.work__list {
  color: #fff;
  text-align: left;
}

main, section:first-of-type {
  padding-top: 264px;
}

.hero {
  color: #fff;
  background-image: url("background.60ce8b9d.webp");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: var(--background-color);
  height: 700px;
  font-family: var(--podkova-font);
  justify-content: center;
  align-items: center;
  margin-top: -120px;
  padding: 300px 0 100px;
  display: flex;
}

@media screen and (width >= 1200px) {
  .hero {
    height: 800px;
    padding-top: 200px;
  }
}

.hero__title {
  font-size: 35px;
}

.hero__description {
  max-width: 600px;
}

.hero__logo {
  border-radius: 20px;
}

.hero__text {
  background-color: var(--background-color);
  border-radius: 10px;
  outline: 2px solid #ff0;
  margin-left: auto;
  padding: 10px 20px;
}

.gena {
  color: #000;
  background-color: #ff0;
  height: 200px;
  margin-top: 0;
  margin-bottom: 0;
}

.gena__title {
  text-align: center;
  padding-top: 50px;
  font-family: Great Vibes, cursive;
  font-size: 54px;
  font-style: normal;
  font-weight: 400;
}

.galery {
  background-color: #f5f5f5;
  padding: 60px 0;
}

.galery__title {
  text-align: center;
  color: #333;
  margin-bottom: 40px;
  font-family: Great Vibes, cursive;
  font-size: 50px;
  font-weight: 400;
}

.galery__container {
  background-color: #fff;
  border-radius: 12px;
  max-width: 900px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 20px #0000001a;
}

.galery__images {
  background: #000;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 500px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.galery__image {
  object-fit: contain;
  opacity: 0;
  z-index: 0;
  border-radius: 6px;
  max-width: 100%;
  max-height: 100%;
  transition: opacity .6s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.galery__image.active {
  opacity: 1;
  z-index: 1;
}

.galery .buttons {
  z-index: 2;
  pointer-events: none;
  justify-content: space-between;
  padding: 0 25px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
}

.galery .button__left, .galery .button__right {
  color: #fff;
  cursor: pointer;
  pointer-events: auto;
  background: #00000080;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  font-size: 24px;
  transition: background-color .3s, transform .2s;
  display: flex;
}

.galery .button__left:hover, .galery .button__right:hover {
  background-color: #000c;
  transform: scale(1.1);
}

.trainers {
  background: #f9f9f9;
  padding: 60px 0;
}

.trainers__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.trainers__item {
  align-items: center;
  gap: 40px;
  display: flex;
  position: relative;
}

.trainers__item:nth-child(2n) {
  flex-direction: row-reverse;
}

@media (width <= 1200px) {
  .trainers__item, .trainers__item:nth-child(2n) {
    flex-direction: column;
  }

  .trainers__item {
    text-align: center;
    gap: 20px;
  }
}

.trainers__item:after {
  content: "";
  background: #9a9a9a;
  width: 100%;
  height: 1px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.trainers__item:not(:last-child):after {
  content: "";
  background: #9a9a9a;
  height: 1px;
  margin-top: 40px;
  display: block;
}

.trainers__title {
  text-align: center;
  color: #333;
  font-size: 36px;
  font-family: var(--podkova-font);
  margin-bottom: 40px;
}

.trainers__img {
  object-fit: cover;
  border-radius: 12px;
  width: 300px;
  box-shadow: 0 4px 16px #0000001a;
}

@media (width >= 1200px) {
  .trainers__img {
    width: 500px;
    height: auto;
  }
}

.trainers__text {
  width: 50%;
  padding: 0 20px;
}

@media (width <= 1200px) {
  .trainers__text {
    width: 100%;
    padding: 0;
  }
}

.trainers__name {
  color: #222;
  text-align: left;
  margin-bottom: 16px;
  font-size: 28px;
  font-weight: 700;
}

@media (width <= 1200px) {
  .trainers__name {
    text-align: center;
  }
}

.trainers__description {
  color: #555;
  white-space: pre-line;
  font-size: 16px;
  line-height: 1.6;
}

@media (width <= 1200px) {
  .trainers__description {
    text-align: center;
  }
}

.flexx {
  flex-direction: column;
  display: flex;
}

.wit {
  height: 600px;
}

@media screen and (width >= 1200px) {
  .wit {
    width: 400px;
  }
}

.prices-section {
  color: #fff;
  text-align: center;
  background-color: #000;
  padding: 60px 20px;
  font-family: Poppins, sans-serif;
}

.section-title {
  color: #f5b800;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-align: center;
  margin-bottom: 20px;
  font-size: 2.5rem;
}

.group-title {
  color: #f5b800;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
  margin-top: 40px;
  margin-bottom: 20px;
  font-size: 1.5rem;
}

.price-list {
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  justify-items: center;
  gap: 25px;
  max-width: 1000px;
  margin: 0 auto 40px;
  padding: 0;
  list-style: none;
  display: grid;
}

.price-card {
  background: #111;
  border: 2px solid #f5b800;
  border-radius: 16px;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  min-height: 220px;
  padding: 25px 20px;
  transition: all .3s;
  display: flex;
  box-shadow: 0 0 20px #f5b80026;
}

.price-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 0 25px #f5b80080;
}

.price-card h4 {
  color: #f5b800;
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.price-card .price {
  color: #fff;
  margin: 8px 0;
  font-size: 1.6rem;
  font-weight: bold;
}

.price-card p {
  color: #ddd;
  font-size: .95rem;
}

.inner-list {
  color: #ddd;
  margin: 0;
  padding: 0;
  list-style: none;
}

.inner-list li {
  margin: 6px 0;
}

@media (width <= 768px) {
  .section-title {
    font-size: 2rem;
  }

  .price-list {
    grid-template-columns: 1fr 1fr;
  }
}

@media (width <= 500px) {
  .price-list {
    grid-template-columns: 1fr;
  }
}

.history {
  color: #f5f5f5;
  background: linear-gradient(#000 0%, #111 40%, #fff 100%);
  padding: 80px 20px;
  font-family: Poppins, sans-serif;
}

.history .container {
  max-width: 1200px;
  margin: 0 auto;
}

.history__title {
  color: #f5b800;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 2px;
  margin-bottom: 60px;
  font-size: 2.5rem;
  position: relative;
}

.history__title:after {
  content: "";
  background: #f5b800;
  border-radius: 2px;
  width: 80px;
  height: 3px;
  margin: 15px auto 0;
  display: block;
}

.history__block {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 40px;
  margin-bottom: 80px;
  display: flex;
}

.history__block.reverse {
  flex-direction: row-reverse;
}

.history__image {
  border: 3px solid #f5b800;
  border-radius: 20px;
  width: 45%;
  transition: all .4s;
  box-shadow: 0 0 25px #f5b80066;
}

.history__image:hover {
  transform: scale(1.03);
  box-shadow: 0 0 35px #f5b800cc;
}

.history__description {
  text-align: left;
  color: #eaeaea;
  width: 50%;
  font-size: 1rem;
  line-height: 1.7;
}

.history__photo {
  justify-content: center;
  display: flex;
}

.history__vovk-before {
  border: 3px solid #f5b800;
  border-radius: 20px;
  width: 70%;
  max-width: 700px;
  transition: all .4s;
  box-shadow: 0 0 25px #f5b80066;
}

.history__vovk-before:hover {
  transform: scale(1.02);
  box-shadow: 0 0 35px #f5b800cc;
}

@media (width <= 992px) {
  .history__block {
    text-align: center;
    flex-direction: column;
  }

  .history__image, .history__description {
    width: 90%;
  }

  .history__description {
    text-align: justify;
  }

  .history__title {
    font-size: 2rem;
  }
}

.article {
  background-color: var(--color-white);
  box-shadow: 0 4px 20px var(--color-shadow);
  border-radius: 16px;
  padding: 40px;
}

.article__title {
  color: var(--color-graphite-dark);
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;
  font-weight: 700;
}

.to-top {
  z-index: 1000;
  backdrop-filter: blur(6px);
  opacity: 0;
  pointer-events: none;
  background: linear-gradient(#ffffff0f, #ffffff05);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 64px;
  height: 64px;
  transition: transform .35s cubic-bezier(.2, .9, .3, 1), opacity .35s, box-shadow .2s;
  display: inline-flex;
  position: fixed;
  bottom: 30px;
  right: 30px;
  transform: translateY(40px)scale(.95);
  box-shadow: 0 8px 24px #00000059;
}

.to-top img {
  width: 100%;
  height: 100%;
  display: block;
}

.to-top.show {
  opacity: 1;
  pointer-events: auto;
  transform: translateY(0)scale(1);
}

.to-top:hover {
  transform: translateY(-4px)scale(1.03);
  box-shadow: 0 12px 30px #00000073;
}

.to-top img {
  object-fit: contain;
  width: 48px;
  height: 48px;
}

.article__description {
  text-align: center;
  color: var(--color-text-secondary);
  margin-bottom: 40px;
  font-size: 1.1rem;
}

.to-top.in-section {
  position: fixed;
  bottom: 16px;
  right: 16px;
}

.article__list-title, .recomandation__title, .prohibitions__title, .duty__title, .membership__title {
  color: var(--color-graphite-dark);
  border-left: 4px solid var(--color-accent);
  margin: 30px 0 15px;
  padding-left: 10px;
  font-size: 1.4rem;
  font-weight: 700;
}

.membership__description {
  color: var(--color-text-secondary);
  margin-bottom: 10px;
  font-style: italic;
}

.article__list, .recomandation__list, .prohibitions__list, .duty__list, .membership__list {
  margin-bottom: 40px;
  padding-left: 20px;
  list-style: inside decimal;
}

.article__item, .recomandation__item, .prohibitions__item, .duty__item, .membership__item {
  background-color: var(--color-bg-light);
  border-left: 4px solid var(--color-border);
  color: var(--background-color);
  border-radius: 8px;
  margin-bottom: 10px;
  padding: 15px 20px;
  transition: all .3s;
}

.article__item:hover, .recomandation__item:hover, .prohibitions__item:hover, .duty__item:hover, .membership__item:hover {
  background-color: var(--color-bg-hover);
  border-left-color: var(--color-accent);
}

.artcle__item-text, .recomandation__text, .prohibitions__text, .duty__text, .membership__text {
  color: var(--color-graphite);
  font-size: 1rem;
  font-weight: 400;
}

.info__text, .info__text2, .info__text3 {
  color: var(--color-graphite-dark);
  background-color: var(--color-bg-light);
  border-left: 4px solid var(--color-accent);
  border-radius: 8px;
  margin-top: 20px;
  padding: 15px 20px;
  font-size: 1rem;
}

@media (width <= 768px) {
  .article {
    padding: 25px;
  }

  .article__title {
    font-size: 1.6rem;
  }

  .article__description {
    font-size: 1rem;
  }
}

@media (width <= 480px) {
  .container {
    padding: 20px 10px;
  }

  .article {
    padding: 20px;
  }

  .article__title {
    font-size: 1.4rem;
  }

  .to-top {
    width: 48px;
    height: 48px;
    bottom: 20px;
    right: 16px;
    transform: translateY(20px)scale(.95);
  }

  .to-top.show {
